n\n// input:not([type=\"submit\"]):not([type=\"reset\"]):not([type=\"button\"]):not([type=\"checkbox\"]):not([type=\"radio\"]):not([type=\"range\"]),\n// textarea,\n// select {\n//   color: var(--form-text);\n//   background-color: var(--background-main);\n//   font-family: inherit;\n//   font-size: inherit;\n//   margin-right: 6px;\n//   margin-bottom: 6px;\n//   padding: 0.5em 1em;\n//   outline: none;\n//   border: none;\n// }\n\n// input:not([type=\"checkbox\"]):not([type=\"radio\"]):focus:not([type=\"range\"]):not([type=\"submit\"]):not([type=\"reset\"]):not([type=\"button\"]),\n// select:focus,\n// textarea:focus {\n//   // box-shadow: 0 0 0 2px var(--focus);\n//   border-bottom: 1px solid var(--focus) !important;\n// }\n\ninput[type=\"color\"] {\n  min-height: 3.4rem;\n  padding: 4px 8px;\n  cursor: pointer;\n}\n\ninput[type=\"checkbox\"],\ninput[type=\"radio\"] {\n  height: 1em;\n  width: 1em;\n  vertical-align: middle;\n}\n\ninput[type=\"radio\"] {\n  border-radius: 100%;\n}\n\ninput {\n  vertical-align: top;\n}\n\nlabel {\n  vertical-align: middle;\n  display: inline-block;\n  // margin-bottom: 4px;\n}\n\ninput:not([type=\"checkbox\"]):not([type=\"radio\"]),\ninput[type=\"range\"],\nselect,\nbutton,\ntextarea {\n  -webkit-appearance: none;\n}\n\ntextarea {\n  display: block;\n  margin-right: 0;\n  box-sizing: border-box;\n  resize: vertical;\n}\n\ntextarea:not([cols]) {\n  width: 100%;\n}\n\ntextarea:not([rows]) {\n  min-height: 40px;\n  height: 140px;\n}\n\nselect {\n  background: var(--background-main) var(--select-arrow) calc(100% - 12px) 50% / 12px no-repeat;\n  padding-right: 35px;\n}\n\nselect::-ms-expand {\n  display: none;\n}\n\nselect[multiple] {\n  padding-right: 10px;\n  background-image: none;\n  overflow-y: auto;\n}\n\nbutton,\ninput[type=\"submit\"],\ninput[type=\"reset\"],\ninput[type=\"button\"] {\n  color: var(--form-text);\n  background-color: var(--background-main);\n  font-family: inherit;\n  font-size: inherit;\n  padding: 0.5em 1em;\n  outline: none;\n  border: none;\n  border-radius: 4px;\n}\n\nbutton:hover,\ninput[type=\"submit\"]:hover,\ninput[type=\"reset\"]:hover,\ninput[type=\"button\"]:hover {\n  background: var(--button-hover);\n}\n\n// input[type=\"checkbox\"]:active,\n// input[type=\"radio\"]:active,\n// input[type=\"submit\"]:active,\n// input[type=\"reset\"]:active,\n// input[type=\"button\"]:active,\n// button:active {\n//   transform: translateY(2px);\n// }\n\ninput:disabled,\nselect:disabled,\nbutton:disabled,\ntextarea:disabled {\n  cursor: not-allowed;\n}\n\nbutton:disabled {\n  opacity: 0.5;\n}\n\n::placeholder {\n  color: var(--form-placeholder);\n}\n\nfieldset {\n  border: 1px var(--focus) solid;\n  border-radius: 6px;\n  margin: 0;\n  margin-bottom: 12px;\n  padding: 10px;\n}\n\nlegend {\n  font-size: 0.9em;\n  font-weight: 600;\n}\n","img,\nvideo {\n  max-width: 100%;\n  height: auto;\n}\n\nhr {\n  border: none;\n  border-top: 1px solid var(--border);\n}\n\ntable {\n  border-collapse: collapse;\n  margin-bottom: 10px;\n  width: 100%;\n  table-layout: auto;\n}\n\ntable caption {\n  text-align: left;\n}\n\ntd,\nth {\n  padding: 6px;\n  text-align: left;\n  vertical-align: top;\n  word-wrap: break-word;\n}\n\nthead {\n  border-bottom: 1px solid var(--border);\n}\n\ntfoot {\n  border-top: 1px solid var(--border);\n}\n\ntbody tr:nth-child(even) {\n  background-color: var(--background-bright);\n}\n\ntbody tr:nth-child(even) button {\n  background-color: var(--background-bright);\n}\n\ntbody tr:nth-child(even) button:hover {\n  background-color: var(--background-body);\n}\n\n::-webkit-scrollbar {\n  height: 12px;\n  width: 12px;\n}\n\n::-webkit-scrollbar-track {\n  background: var(--background-main);\n  // border-radius: 6px;\n}\n\n::-webkit-scrollbar-thumb {\n  border-radius: 6px;\n  border: 2px solid var(--background-main);\n}\n\n::-webkit-scrollbar-thumb:not(:hover) {\n  background: var(--scrollbar-thumb);\n}\n\n::-webkit-scrollbar-thumb:hover {\n  background: var(--scrollbar-thumb-hover);\n}\n\n::selection {\n  background-color: var(--selection);\n  color: var(--text-selection);\n}\n\ndetails {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n  background-color: var(--background-bright);\n  padding: 10px 10px 0;\n  margin: 1em 0;\n  border-radius: 6px;\n  overflow: hidden;\n}\n\ndetails[open] {\n  padding: 10px;\n}\n\ndetails > :last-child {\n  margin-bottom: 0;\n}\n\ndetails[open] summary {\n  margin-bottom: 10px;\n}\n\nsummary {\n  display: list-item;\n  background-color: var(--background-main);\n  padding: 10px;\n  margin: -10px -10px 0;\n  cursor: pointer;\n  outline: none;\n}\n\nsummary:hover,\nsummary:focus {\n  text-decoration: underline;\n}\n\ndetails > :not(summary) {\n  margin-top: 0;\n}\n\nsummary::-webkit-details-marker {\n  color: var(--text-main);\n}\n\ndialog {\n  background-color: var(--background-bright);\n  color: var(--text-main);\n  border: none;\n  border-radius: 6px;\n  border-color: var(--border);\n  padding: 10px 30px;\n}\n\ndialog > header:first-child {\n  background-color: var(--background-main);\n  border-radius: 6px 6px 0 0;\n  margin: -10px -30px 10px;\n  padding: 10px;\n  text-align: center;\n}\n\ndialog::backdrop {\n  background: #0000009c;\n  backdrop-filter: blur(4px);\n}\n\nfooter {\n  border-top: 1px solid var(--border);\n  padding-top: 10px;\n  color: var(--text-muted);\n}\n\nbody > footer {\n  margin-top: 40px;\n}\n","@media print {\n  body,\n  pre,\n  code,\n  summary,\n  details,\n  button,\n  input,\n  textarea {\n    background-color: #fff;\n  }\n\n  button,\n  input,\n  textarea {\n    border: 1px solid #000;\n  }\n\n  body,\n  h1,\n  h2,\n  h3,\n  h4,\n  h5,\n  h6,\n  pre,\n  code,\n  button,\n  input,\n  textarea,\n  footer,\n  summary,\n  strong {\n    color: #000;\n  }\n\n  summary::marker {\n    color: #000;\n  }\n\n  summary::-webkit-details-marker {\n    color: #000;\n  }\n\n  tbody tr:nth-child(even) {\n    background-color: #f2f2f2;\n  }\n\n  a {\n    color: #00f;\n    text-decoration: underline;\n  }\n}\n","$range-slider-shadow: 0 1px 1px #000, 0 0 1px #0d0d0d;\n\n@mixin range-track() {\n  width: 100%;\n  height: 9.5px;\n  transition: 0.2s;\n  background: var(--background-main);\n  border-radius: 3px;\n}\n@mixin range-thumb() {\n  -webkit-appearance: none;\n  height: 20px;\n  width: 20px;\n  border-radius: 50%;\n  background: var(--border);\n  margin-top: -7px;\n}\n\ninput[type=\"range\"] {\n  margin: 10px 0;\n  padding: 10px 0;\n  background: transparent;\n}\n\ninput[type=\"range\"]:focus {\n  outline: none;\n}\n\ninput[type=\"range\"]::-webkit-slider-runnable-track {\n  @include range-track();\n}\ninput[type=\"range\"]::-moz-range-track {\n  @include range-track();\n}\ninput[type=\"range\"]::-ms-track {\n  @include range-track();\n}\n\ninput[type=\"range\"]::-webkit-slider-thumb {\n  @include range-thumb();\n}\ninput[type=\"range\"]::-moz-range-thumb {\n  @include range-thumb();\n}\ninput[type=\"range\"]::-ms-thumb {\n  @include range-thumb();\n}\n\ninput[type=\"range\"]:focus::-webkit-slider-thumb {\n  box-shadow: $range-slider-shadow;\n}\ninput[type=\"range\"]:focus::-moz-range-thumb {\n  box-shadow: $range-slider-shadow;\n}\ninput[type=\"range\"]:focus::-ms-thumb {\n  box-shadow: $range-slider-shadow;\n}\n\ninput[type=\"range\"]:focus::-webkit-slider-runnable-track {\n  background: var(--background-main);\n}\ninput[type=\"range\"]:focus::-ms-fill-lower {\n  background: var(--background-main);\n}\ninput[type=\"range\"]:focus::-ms-fill-upper {\n  background: var(--background-main);\n}\n\n// Extra MS overrides\ninput[type=\"range\"]::-ms-track {\n  background: transparent;\n  border-color: transparent;\n  border-width: 16px 0;\n  color: transparent;\n}\ninput[type=\"range\"]::-ms-fill-lower {\n  background: var(--background-main);\n  border: 0.2px solid #010101;\n  border-radius: 3px;\n  box-shadow: 1px 1px 1px #000, 0 0 1px #0d0d0d;\n}\ninput[type=\"range\"]::-ms-fill-upper {\n  background: var(--background-main);\n  border: 0.2px solid #010101;\n  border-radius: 3px;\n  box-shadow: 1px 1px 1px #000, 0 0 1px #0d0d0d;\n}\n","@import \"~novo-design-tokens/scss\";\n\nhtml {\n  scrollbar-color: var(--scrollbar-thumb) var(--background-body);\n  scrollbar-width: none;\n  font-family: var(--font-family-base);\n  font-size: var(--font-size-base, 10px);\n  color: var(--font-color-base, #333);\n}\n\nbody {\n  font-family: var(--font-family-body);\n  font-size: var(--font-size-body, 1.2rem);\n  line-height: 1.2;\n  word-wrap: break-word;\n  color: var(--text-main);\n  background: var(--background-body);\n  text-rendering: optimizeLegibility;\n}\n\nbutton,\ninput,\ntextarea {\n  transition: background-color var(--animation-duration) linear, border-color var(--animation-duration) linear,\n    color var(--animation-duration) linear, box-shadow var(--animation-duration) linear, transform var(--animation-duration) ease;\n}\n","// Hyperlinks\n// --------------------------------------------------\na {\n  // color: $font-color-link;\n  color: var(--links);\n  font-weight: 500;\n  text-decoration: none;\n  cursor: pointer;\n  transition: color 200ms ease-in-out;\n  &:visited {\n    color: $font-color-link;\n  }\n  &:active,\n  &:hover {\n    color: darken($font-color-link, 10%);\n  }\n}\n","/**\n * Flex Grid -\n * Version: 0.3.2\n *\n * Simple grid built with flex box and sass.\n *\n * Matthew Simo - matthew.a.simo@gmail.com\n */\n\n/**\n * Grid setup\n *\n * The grid will calculate dimensions based on these two variables:\n * $fg-columns will inform the grid loops how many columns there should be.\n * $fg-gutter will inform the grid loops how big eac column's gutters should be.\n *\n * The grid will name columns, rows, offsets based on these three variables:\n * $fg-class-row string used for the row class\n * $fg-class-col string used for the column class\n * $fg-class-off string used for the offset class\n */\n$fg-columns: 12 !default;\n$fg-gutter: 0rem !default;\n$fg-class-row: \"row\" !default;\n$fg-class-col: \"col\" !default;\n$fg-class-off: \"off\" !default;\n\n/**\n * Break point namespace object\n *\n * Set the default namespace object with these defaults with the\n * understanding that you can pass in whatever you might require for your site.\n *\n * $fg-breakpoints is a Sass list with nested lists inside. Each sub list defines two things.\n * 1. The namespace for that breakpoint. (Required) (i.e. xs, sm, md, lg)\n * 2. The min-width measurement for the breakpoint for that namespace. (i.e. 48em, 62em, 75em)\n *\n * Note: These should be in the proper order (at least till libsass handles map keys properly).\n *\n * Note: If the measurement is left out then it will be skipped when generating\n * the grid and applied to global styles.\n *\n */\n$fg-breakpoints: ((xs), (sm, 768px), (md, 992px), (lg, 1200px)) !default;\n\n/**\n * Class Name Defaults\n *\n * Define class names for columns, rows and offsets in case compatibility with other\n * libraries is necessary.\n\n/**\n * Calculate column size percentage\n */\n@function get-col-percent($column-num) {\n  @return $column-num / $fg-columns * 100%;\n}\n\n/**\n * Spacing mixin to create uniform margin/padding\n */\n@mixin spacing($value, $type: margin, $orientation: vertical) {\n  @if $orientation == vertical {\n    #{ $type }-top: $value;\n    #{ $type }-bottom: $value;\n  } @else {\n    #{ $type }-left: $value;\n    #{ $type }-right: $value;\n  }\n}\n\n/**\n * Row wrapper class, flex box parent.\n */\n.#{ $fg-class-row } {\n  box-sizing: border-box;\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n  @include spacing((-$fg-gutter), margin, horizontal);\n}\n\n%#{ $fg-class-col } {\n  box-sizing: border-box;\n  display: flex;\n  flex-direction: column;\n  flex-grow: 0;\n  flex-shrink: 0;\n  @include spacing(($fg-gutter), padding, horizontal);\n}\n\n%#{ $fg-class-col }-flex {\n  @extend %#{ $fg-class-col };\n  flex-grow: 1;\n  flex-basis: 0;\n  max-width: 100%;\n}\n\n/**\n * Generate a set of grid column classes using a namespace\n *\n * .#{ $fg-class-col }-[namespace] for intelligent column division\n * .#{ $fg-class-col }-[namespace]-[number] for a column that covers a specific number of columns (e.g. 1-12 by default)\n * .#{ $fg-class-off }-[namespace]-[number] for pushing a col a specific number of columns (e.g. 1-11 by default)\n */\n@mixin grid-base($namespace) {\n  .#{ $fg-class-col }-#{$namespace} {\n    @extend %#{ $fg-class-col }-flex;\n  }\n  @for $i from 1 through $fg-columns {\n    .#{ $fg-class-col }-#{$namespace}-#{$i} {\n      @extend %#{ $fg-class-col };\n    }\n  }\n}\n\n@mixin grid-sizes($namespace) {\n  // Run the loop for as many columns as speficied\n  @for $i from 1 through $fg-columns {\n    .#{ $fg-class-col }-#{$namespace}-#{$i} {\n      flex-basis: get-col-percent($i);\n      max-width: get-col-percent($i);\n    }\n  }\n  @for $i from 1 through $fg-columns {\n    // Don't include the .off class for the last column\n    @if ($i != $fg-columns) {\n      .#{ $fg-class-off }-#{$namespace}-#{$i} {\n        margin-left: get-col-percent($i);\n      }\n    }\n  }\n}\n\n/**\n * Build the grid in two steps, to help minimize file size\n * Step 1, for each namespace, create the grid-base\n * Step 2, for each namespace, wrap the col width/offset measurements in their breakpoint media query\n */\n@each $val in $fg-breakpoints {\n  $namespace: nth($val, 1);\n  @include grid-base($namespace);\n}\n\n@each $val in $fg-breakpoints {\n  @if length($val) == 1 {\n    $namespace: nth($val, 1);\n    @include grid-sizes($namespace);\n  } @else {\n    $namespace: nth($val, 1);\n    $size: nth($val, 2);\n    @media only screen and (min-width: #{$size}) {\n      @include grid-sizes($namespace);\n    }\n  }\n}\n","@import \"./variables.scss\";\n@import \"../elements/common/typography/text.mixins.scss\";\n\n// Headers\n// --------------------------------------------------\nh1 {\n  font-size: 2.5rem;\n  font-weight: 400;\n  margin: 0;\n  padding: 0.45rem 0 0.35rem;\n}\n\nh2 {\n  font-size: 2rem;\n  font-weight: 500;\n  margin: 0;\n  padding: 0.75rem 0 0.3rem;\n}\n\nh3 {\n  font-size: 1.75rem;\n  font-weight: 400;\n  margin: 0;\n  padding: 0.6rem 0 0.4rem;\n}\n\nh4 {\n  font-size: 1.375rem;\n  font-weight: 400;\n  margin: 0;\n  padding: 0.75rem 0 0.5rem;\n}\n\nh5 {\n  font-size: 1.125rem;\n  font-weight: 700;\n  margin: 0;\n  padding: 0.75rem 0 0.25rem;\n  text-transform: uppercase;\n  color: var(--text-muted);\n}\n\nh6 {\n  font-size: 1.125rem;\n  font-weight: 500;\n  margin: 0;\n  padding: 0.75rem 0 0.25rem;\n}\n\n// Body And Paragraph\n// --------------------------------------------------\np {\n  font-size: 1.4rem;\n  line-height: 1.375em;\n  margin: 0;\n  padding: 0.25rem 0 0.55rem;\n}\n\nq::before {\n  content: none;\n}\n\nq::after {\n  content: none;\n}\n\nblockquote,\nq {\n  border-left: 4px solid var(--focus);\n  margin: 1.5em 0;\n  padding: 0.5em 1em;\n  font-style: italic;\n}\n\nblockquote > footer {\n  font-style: normal;\n  border: 0;\n}\n\nblockquote cite {\n  font-style: normal;\n}\n\naddress {\n  font-style: normal;\n}\n\nmark {\n  background-color: var(--highlight);\n  border-radius: 2px;\n  padding: 0 2px 0 2px;\n  color: #000;\n}\n\n// Special Classes\n// --------------------------------------------------\n.caption {\n  font-size: 1.125rem;\n  font-weight: 700;\n  text-transform: uppercase;\n  opacity: 0.55;\n  letter-spacing: 0.1px;\n}\n\nh1.novo-section-header,\nh2.novo-section-header,\nh3.novo-section-header,\nh4.novo-section-header,\nh5.novo-section-header,\nh6.novo-section-header {\n  background: var(--background-bright, $color-white);\n  box-sizing: content-box;\n  font-weight: 400;\n  display: flex;\n  width: 100%;\n  padding: 1rem 0.5rem 1rem 1.5rem;\n  margin-left: -1.5rem;\n  margin-right: -1.5rem;\n  > i {\n    display: flex;\n    margin-right: 10px;\n    &.bhi-section {\n      // Special case for icons that are not vertically centered\n      margin-top: -0.2rem;\n    }\n  }\n}\n\n// Utility classes\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nspan {\n  @include base-text-rules();\n  vertical-align: baseline;\n}\n\nspan > span {\n  vertical-align: baseline;\n}\n","@import \"../../../styles/variables.scss\";\n\n@mixin text-disabled($property) {\n  pointer-events: none;\n  color: $disabled;\n}\n\n@mixin base-text-rules() {\n  transition-property: color, opacity;\n  transition: 0.2s ease-out;\n  vertical-align: middle;\n\n  &.text-nowrap {\n    white-space: nowrap;\n  }\n  &.text-ellipsis {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n\n  @include text-sizing();\n  @include text-colors();\n  @include text-margins();\n  @include text-lengths();\n  @include text-weights();\n}\n\n@mixin text-sizing() {\n  // Controls font-size of text\n  &.text-size-default {\n    font-size: inherit;\n  }\n  &.text-size-body {\n    font-size: $font-size-body;\n  }\n  &.text-size-xs {\n    font-size: $font-size-xs;\n  }\n  &.text-size-sm {\n    font-size: $font-size-sm;\n  }\n  &.text-size-md {\n    font-size: $font-size-md;\n  }\n  &.text-size-lg {\n    font-size: $font-size-lg;\n  }\n  &.text-size-xl {\n    font-size: $font-size-xl;\n  }\n  &.text-size-2xl {\n    font-size: $font-size-2xl;\n  }\n  &.text-size-3xl {\n    font-size: $font-size-3xl;\n  }\n  &.text-size-smaller {\n    font-size: 0.8em;\n  }\n  &.text-size-larger {\n    font-size: 1.2em;\n  }\n}\n\n@mixin text-weights() {\n  // Font Weight Utils\n  &.text-weight-hairline {\n    font-weight: 100;\n  }\n  &.text-weight-thin {\n    font-weight: 200;\n  }\n  &.text-weight-light {\n    font-weight: 300;\n  }\n  &.text-weight-normal {\n    font-weight: 400;\n  }\n  &.text-weight-medium {\n    font-weight: 500;\n  }\n  &.text-weight-semibold {\n    font-weight: 600;\n  }\n  &.text-weight-bold {\n    font-weight: 700;\n  }\n  &.text-weight-extrabold {\n    font-weight: 800;\n  }\n  &.text-weight-heavy {\n    font-weight: 900;\n  }\n  &.text-weight-lighter {\n    font-weight: lighter;\n  }\n  &.text-weight-bolder {\n    font-weight: bolder;\n  }\n}\n\n@mixin text-lengths() {\n  &.text-length-small {\n    max-width: 40ch;\n  }\n  &.text-length-medium {\n    max-width: 55ch;\n  }\n  &.text-length-large {\n    max-width: 70ch;\n  }\n}\n\n@mixin text-colors() {\n  @include theme-colors() using ($name, $color, $contrast, $tint, $shade, $pale) {\n    &.text-color-#{$name} {\n      color: $color;\n    }\n  }\n}\n\n@mixin text-margins() {\n  &.margin-before {\n    margin-top: 0.4rem;\n  }\n\n  &.margin-after {\n    margin-bottom: 0.8rem;\n  }\n}\n\n@mixin novo-body-text() {\n  display: inline;\n  font-weight: normal;\n  color: inherit;\n  font-size: var(--font-size-text);\n  @include base-text-rules();\n}\n\n@mixin novo-title-text() {\n  font-weight: 500;\n  line-height: 1.5;\n  color: var(--text-main, $dark);\n  white-space: nowrap;\n  text-overflow: ellipsis;\n  font-size: var(--font-size-title);\n  @include base-text-rules();\n}\n\n@mixin novo-label-text() {\n  font-weight: 500;\n  // text-transform: uppercase;\n  word-break: word-break;\n  overflow-wrap: break-word;\n  line-height: 1.375;\n  color: var(--text-muted);\n  font-size: var(--font-size-label);\n  @include base-text-rules();\n}\n\n@mixin novo-caption-text() {\n  font-size: var(--font-size-caption);\n  font-weight: 400;\n  line-height: 1.375;\n  color: var(--text-muted);\n  @include base-text-rules();\n}\n","// Iconography\n// --------------------------------------------------\n$icon-height: 1.7rem; // 24px Base background size\n$icon-width: 1.7rem; // 24px Base background size\n$base-icon-size: 1.4rem; //18px Base font-size\ni {\n  font-size: $base-icon-size;\n  display: inline-table;\n  flex: 0 0 auto;\n  &[theme] {\n    &[theme=\"contained\"] {\n      font-size: 0.8rem;\n      border-radius: 0.55rem;\n      height: $icon-height;\n      width: $icon-width;\n      text-align: center;\n      &:before {\n        vertical-align: baseline;\n        line-height: 1.8;\n      }\n      @each $entity, $colo