2 !default;\n$whiteframe-shadow-z3: 0px 4px 10px rgba(0, 0, 0, 0.15) !default;\n$whiteframe-zindex-z3: 3 !default;\n$whiteframe-shadow-z4: 0px 16px 28px 0 rgba(0, 0, 0, 0.22) !default;\n$whiteframe-zindex-z4: 4 !default;\n$whiteframe-shadow-z5: 0px 27px 24px 0 rgba(0, 0, 0, 0.2) !default;\n$whiteframe-zindex-z5: 5 !default;\n\n// Z-indexes\n// -------------------------\n// Function to call z-indexes.\n// Usage: z-index: z('modal');\n@function z($layer) {\n  $z-layers: (\n    \"max\": 9001,\n    \"overlay\": 1000,\n    \"modal\": 500,\n    \"tooltip\": 100,\n    \"toast\": 90,\n    \"dialogue\": 80,\n    \"bottom-sheet\": 70,\n    \"aside\": 61,\n    \"sidenav\": 60,\n    \"backdrop\": 50,\n    \"fab\": 20,\n    \"above\": 10,\n    \"default\": 1,\n    \"below\": -1,\n    \"bottom\": -9000,\n  );\n\n  @if not map-has-key($z-layers, $layer) {\n    @warn 'No layer found for `#{$layer}` in $z-layers map. Property omitted.';\n  }\n\n  @return map-get($z-layers, $layer);\n}\n\n// Easing Curves\n// -------------------------\n// Usage: html-tag { @include ease('all', 'swift-ease-in-out'); }\n$easing-curves: (\n  \"swift-ease-out\": (\n    \"duration\": 400ms,\n    \"curve\": \"cubic-bezier(0.25, 0.8, 0.25, 1)\",\n  ),\n  \"swift-ease-in\": (\n    \"duration\": 300ms,\n    \"curve\": \"cubic-bezier(0.55, 0, 0.55, 0.2)\",\n  ),\n  \"swift-ease-in-out\": (\n    \"duration\": 500ms,\n    \"curve\": \"cubic-bezier(0.35, 0, 0.25, 1)\",\n  ),\n);\n\n@function map-deep-get($map, $keys...) {\n  @each $key in $keys {\n    $map: map-get($map, $key);\n  }\n\n  @return $map;\n}\n\n@mixin ease($target, $curve-name) {\n  $duration: map-deep-get($easing-curves, $curve-name, \"duration\");\n  $curve: map-deep-get($easing-curves, $curve-name, \"curve\");\n  transition: #{$target $duration $curve};\n}\n","// We want overlays to always appear over user content, so set a baseline\n// very high z-index for the overlay container, which is where we create the new\n// stacking context for all overlays.\n$cdk-z-index-overlay-container: z(overlay);\n$cdk-z-index-overlay: z(overlay);\n$cdk-z-index-overlay-backdrop: z(backdrop);\n\n// Background color for all of the backdrops\n$cdk-overlay-dark-backdrop-background: rgba(0, 0, 0, 0.6);\n\n// Default backdrop animation is based on the Material Design swift-ease-out.\n$backdrop-animation-duration: 400ms !default;\n$backdrop-animation-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1) !default;\n\n@mixin cdk-overlay() {\n  .cdk-overlay-container,\n  .cdk-global-overlay-wrapper {\n    // Disable events from being captured on the overlay container.\n    pointer-events: none;\n\n    // The container should be the size of the viewport.\n    top: 0;\n    left: 0;\n    height: 100%;\n    width: 100%;\n  }\n\n  // The overlay-container is an invisible element which contains all individual overlays.\n  .cdk-overlay-container {\n    position: fixed;\n    z-index: $cdk-z-index-overlay-container;\n\n    &:empty {\n      // Hide the element when it doesn't have any child nodes. This doesn't\n      // include overlays that have been detached, rather than disposed.\n      display: none;\n    }\n  }\n\n  // We use an extra wrapper element in order to use make the overlay itself a flex item.\n  // This makes centering the overlay easy without running into the subpixel rendering\n  // problems tied to using `transform` and without interfering with the other position\n  // strategies.\n  .cdk-global-overlay-wrapper {\n    display: flex;\n    position: absolute;\n    z-index: $cdk-z-index-overlay;\n  }\n\n  // A single overlay pane.\n  .cdk-overlay-pane {\n    position: absolute;\n    pointer-events: auto;\n    box-sizing: border-box;\n    z-index: $cdk-z-index-overlay;\n  }\n\n  .cdk-overlay-backdrop {\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    z-index: $cdk-z-index-overlay-backdrop;\n    pointer-events: auto;\n    -webkit-tap-highlight-color: transparent;\n    transition: opacity $backdrop-