t-size: 1em;\n            opacity: 0.6;\n            padding: 1rem;\n            flex: 1;\n            &.active {\n              opacity: 1;\n              background-color: var(--selection);\n              color: $white;\n            }\n          }\n        }\n      }\n    }\n  }\n  .digital--inner {\n    display: flex;\n    text-align: center;\n    align-items: center;\n    justify-content: center;\n  }\n  .digital--clock {\n    color: #fff;\n    font-size: 1.8em;\n    font-weight: 500;\n  }\n  .control-block {\n    display: inline-block;\n  }\n  .digital--period {\n    display: block;\n    color: #fff;\n    cursor: pointer;\n    font-size: 1em;\n    opacity: 0.6;\n    text-transform: uppercase;\n    font-weight: 400;\n    &.active {\n      opacity: 1;\n      font-weight: 600;\n    }\n  }\n\n  .increments {\n    position: relative;\n    height: 250px;\n    width: auto;\n    display: flex;\n    flex-flow: row nowrap;\n\n    novo-list {\n      overflow-y: auto;\n      overflow-x: hidden;\n      height: 100%;\n      flex: 1;\n      scroll-snap-type: y mandatory;\n      -ms-overflow-style: none; /* Internet Explorer 10+ */\n      scrollbar-width: none; /* Firefox */\n      &::-webkit-scrollbar {\n        display: none; /* Safari and Chrome */\n      }\n    }\n\n    .increments--hour,\n    .increments--minute,\n    .increments--meridian {\n      padding: 5px 16px;\n      width: 50px;\n      scroll-snap-align: start;\n\n      .list-item {\n        line-height: 19px;\n      }\n      &:focus,\n      &:hover {\n        background: var(--selection);\n        color: var(--text-selection);\n        filter: brightness(1.25);\n      }\n      &.active {\n        background: var(--selection);\n        color: var(--text-selection);\n        font-weight: 500;\n      }\n    }\n  }\n\n  /* ANALOG CLOCK\n    ----------------------------- */\n  .analog {\n    height: 250px;\n    width: 250px;\n    position: relative;\n    margin: 10% auto;\n  }\n  .analog--inner {\n    top: 0;\n    lef