   position: absolute;\n  }\n  & > .tile-container {\n    display: flex;\n    text-align: center;\n    background-color: var(--background-bright);\n    border: solid thin $ocean;\n    border-radius: $border-radius;\n    position: relative;\n    align-items: center;\n    .tile {\n      padding: 0 $spacing-md;\n      line-height: 3.2rem;\n      height: 100%;\n      z-index: 1;\n      position: relative;\n      cursor: pointer;\n      letter-spacing: 0.02em;\n      &:not(:last-child) {\n        border-right: solid thin $ocean;\n      }\n      &:not(.disabled) {\n        color: $ocean;\n      }\n      &:not(.disabled).active {\n        box-shadow: inset 3px 2px 4px 0px rgba(0, 0, 0, 0.3);\n        color: $white;\n        background: $ocean;\n      }\n      &.active {\n        font-weight: 600;\n      }\n      &:hover {\n        box-shadow: 0 3px 7px rgba(0, 0, 0, 0.15), 0 1px 2px rgba(0, 0, 0, 0.15);\n      }\n      &.disabled {\n        cursor: not-allowed;\n      }\n      label {\n        z-index: 1;\n        position: relative;\n        cursor: inherit;\n      }\n    }\n    &.active {\n      color: $positive;\n      border-color: $positive;\n      box-shadow: 0px 0px 15px 3px rgba(74, 137, 220, 0.25);\n    }\n    &.disabled {\n      border-color: $disabled-color;\n      color: $disabled-color;\n    }\n  }\n}\n","$tooltip-background: var(--tooltip-background-color, #383838);\n$tooltip-error-color: var(--color-shade-error, #b34e4d);\n$tooltip-info-color: var(--color-shade-info, #3986ac);\n$tooltip-warning-color: var(--color-shade-warning, #c09854);\n$tooltip-success-color: var(--color-shade-success, #458746);\n\n@mixin tooltip-pointer($color) {\n  &.top-right:before,\n  &.top-left:before,\n  &.top:before {\n    border-top-color: $color;\n  }\n  &.bottom-left::before,\n  &.bottom-right:before,\n  &.bottom:before {\n    border-bottom-color: $color;\n  }\n  &.left:before {\n    border-left-color: $color;\n  }\n  &.right:before {\n    border-right-color: $color;\n  }\n}\n\nnovo-tooltip {\n  div {\n    background: $tooltip-background;\n    color: var(--color-white, #fff);\n    border-radius: var(--tooltip-border-radius, 4px);\n    padding: 8px 10px;\n    font-size: 12px;\n    line-height: 12px;\n    white-space: nowrap;\n    box-shadow: var(--shadow-2);\n\n    &.error {\n      background-color: $tooltip-error-color;\n      @include tooltip-pointer($tooltip-error-color);\n    }\n    &.info {\n      background-color: $tooltip-info-color;\n      @include tooltip-pointer($tooltip-info-color);\n    }\n    &.warning {\n      background-color: $tooltip-warning-color;\n      @include tooltip-pointer($tooltip-warning-color);\n    }\n    &.success {\n      background-color: $tooltip-success-color;\n      @include tooltip-pointer($tooltip-success-color);\n    }\n\n    //Arrow positions\n    @include tooltip-pointer($tooltip-background);\n\n    &.top:before {\n      margin-bottom: -11px;\n      left: calc(50% - 6px);\n      bottom: 0;\n    }\n\n    &.top-left:before {\n      margin-right: 0;\n      margin-bottom: -11px;\n      right: 1px;\n      bottom: 0;\n    }\n\n    &.top-right:before {\n      margin-left: 0;\n      margin-bottom: -11px;\n      left: 1px;\n      bottom: 0;\n    }\n\n    &.bottom:before {\n      margin-top: -11px;\n      left: calc(50% - 6px);\n      top: 0;\n    }\n\n    &.bottom-left:before {\n      margin-right: 0;\n      margin-top: -11px;\n      right: 1px;\n      top: 0;\n    }\n\n    &.bottom-right:before {\n      margin-left: 0;\n      margin-top: -11px;\n      left: 1px;\n      top: 0;\n    }\n\n    &.left:before {\n      margin-right: -11px;\n      margin-bottom: -6px;\n      right: 0;\n      bottom: 50%;\n    }\n\n    &.right:before {\n      left: 0;\n      bottom: 50%;\n      margin-left: -11px;\n      margin-bottom: -6px;\n    }\n\n    &:before {\n      content: \"\";\n      position: absolute;\n      background: 0 0;\n      border: 6px solid transparent;\n      box-sizing: border-box;\n    }\n\n    // Sizing\n   