\n      overflow: hidden;\n      max-height: inherit;\n      max-width: 400px;\n      min-width: 400px;\n      display: block;\n      z-index: z(\"max\");\n      border-radius: 2px;\n      top: 100%;\n      margin-top: 5px;\n    }\n  }\n  .dropdown-container {\n    background-color: white;\n    list-style: none;\n    line-height: 26px;\n    display: none;\n    margin: 0;\n    max-height: 0;\n    position: absolute;\n    padding: 0;\n    transform: translateY(0);\n    transition: all 0.15s cubic-bezier(0.35, 0, 0.25, 1);\n    min-width: 180px;\n    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);\n    .novo-category-dropdown-search {\n      width: 100%;\n      padding: 5px;\n      border-bottom: 1px solid #ccc;\n      position: relative;\n      input {\n        font-size: 1em;\n        background: transparent !important;\n        border: none;\n        border-bottom: 1px solid lighten($dark, 45%);\n        border-radius: 0;\n        outline: none;\n        height: 2rem;\n        width: 95%;\n        margin: 0;\n        padding: 0;\n        box-shadow: none;\n        box-sizing: content-box;\n        transition: all 300ms;\n        color: #26282b;\n        margin-left: 10px;\n        &:hover {\n          border-bottom: 1px solid lighten($dark, 15%);\n        }\n        &:focus {\n          border-bottom: 1px solid $positive;\n        }\n        &::placeholder {\n          color: var(--form-placeholder);\n        }\n      }\n      i.bhi-search,\n      i.bhi-times {\n        position: absolute;\n        bottom: 15px;\n        right: 5px;\n        color: #aaaaaa;\n        font-size: 1.2rem;\n        margin-right: 10px;\n      }\n      i.bhi-times {\n        cursor: pointer;\n        font-size: 1.2rem;\n      }\n    }\n    footer {\n      padding: 5px 23px;\n      border-top: 1px solid #ccc;\n      display: flex;\n      justify-content: flex-end;\n      > a {\n        margin-left: 10px;\n        margin-right: