: 1px;\n        margin-left: -1rem;\n        border-bottom-width: 0;\n        border-top-color: var(--background-bright, $white);\n      }\n      &.center {\n        left: 50%;\n      }\n      &.left {\n        left: 91%;\n      }\n      &.right {\n        left: 9%;\n      }\n    }\n    &.right > .arrow {\n      left: -9px;\n      margin-top: -9px;\n      border-left-width: 0;\n      border-right-color: rgba(0, 0, 0, 0.1);\n      &:before {\n        content: \" \";\n        left: 1px;\n        bottom: -1rem;\n        border-left-width: 0;\n        border-right-color: var(--background-bright, $white);\n      }\n      &.center {\n        top: 50%;\n      }\n      &.top {\n        top: 91%;\n      }\n      &.bottom {\n        top: 9%;\n      }\n    }\n    &.bottom > .arrow {\n      margin-left: -9px;\n      border-top-width: 0;\n      border-bottom-color: rgba(0, 0, 0, 0.1);\n      top: -9px;\n      &:before {\n        content: \" \";\n        top: 1px;\n        margin-left: -1rem;\n        border-top-width: 0;\n        border-bottom-color: var(--background-bright, $white);\n      }\n      &.center {\n        left: 50%;\n      }\n      &.left {\n        left: 91%;\n      }\n      &.right {\n        left: 9%;\n      }\n    }\n    &.left > .arrow {\n      right: -9px;\n      margin-top: -9px;\n      border-right-width: 0;\n      border-left-color: rgba(0, 0, 0, 0.1);\n      &:before {\n        content: \" \";\n        right: 1px;\n        border-right-width: 0;\n        border-left-color: var(--background-bright, $white);\n        bottom: -1rem;\n      }\n      &.center {\n        top: 50%;\n      }\n      &.top {\n        top: 91%;\n      }\n      &.bottom {\n        top: 9%;\n      }\n    }\n  }\n  .popover > .arrow {\n    border-width: 9px;\n    &,\n    &:before {\n      position: absolute;\n      display: block;\n      width: 0;\n      height: 0;\n      border-color: transparent;\n      border-style: solid;\n    }\n    &:before {\n      border-width: 1rem;\n      con