-size: 1.8rem;\n    height: 4rem;\n    grid-template-columns: 4rem 1fr;\n  }\n\n  > input {\n    height: 100%;\n    max-width: 400px;\n    display: block;\n    background: transparent;\n    position: relative;\n    font-size: 1.4em;\n    color: var(--text-main);\n    z-index: z(above);\n    border: none;\n    transition: all 250ms;\n    backface-visibility: hidden;\n\n    &:focus,\n    &:active {\n      outline: none;\n    }\n  }\n\n  novo-icon {\n    color: var(--text-main);\n  }\n\n  &.focused {\n    border: 1px solid var(--selection);\n\n    > novo-icon {\n      color: var(--selection) !important;\n    }\n\n    @include theme-colors() using ($name, $color, $contrast, $tint, $shade, $pale) {\n      &[color=\"#{$name}\"] {\n        > novo-icon {\n          color: $color !important;\n        }\n      }\n    }\n  }\n}\n","google-places-list {\n  novo-list {\n    border: 1px solid #4a89dc;\n    novo-list-item {\n      cursor: pointer;\n      flex: 0 0;\n      transition: background-color 250ms;\n      & > div {\n        width: 100%;\n      }\n      &.active {\n        background-color: lighten($positive, 35%);\n      }\n      &:hover {\n        background-color: lighten($positive, 39%);\n      }\n      item-content {\n        flex-flow: row wrap;\n        & > * {\n          flex: 0 0 33%;\n          white-space: nowrap;\n        }\n      }\n    }\n  }\n}\n","novo-value {\n  display: flex;\n  flex-direction: row;\n  max-width: 500px;\n  justify-content: space-between;\n  align-items: flex-start;\n  width: max-content;\n  padding: 8px;\n  @include entity-colors() using ($name, $color, $contrast, $tint, $shade, $pale) {\n    i.#{$name} {\n      color: $color;\n    }\n  }\n  &.horizontal {\n    width: 100%;\n    max-width: 100%;\n    .value-outer {\n      display: grid;\n      width: 100%;\n      grid-template-columns: minmax(120px, 30%) 1fr;\n    }\n  }\n\n  .value-outer {\n    display: flex;\n    flex-direction: column;\n    gap: 0.4rem;\n  }\n\n  .actions {\n    i {\n 