x;\n  align-items: center;\n  + novo-simple-empty-header-cell {\n    border-left: none;\n  }\n  > label {\n    display: inline-block;\n    padding-right: 10px;\n    cursor: pointer;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    &.sort-disabled {\n      cursor: default;\n    }\n  }\n  > div {\n    width: 60px;\n  }\n  novo-dropdown[side=\"right\"] {\n    display: inline-block;\n  }\n}\n\nnovo-simple-empty-header-cell {\n  &.button-header-cell {\n    min-width: 40px;\n  }\n  &.dropdown-header-cell {\n    min-width: 98px;\n  }\n}\n\n.novo-simple-button-cell,\n.novo-simple-dropdown-cell {\n  display: flex;\n  align-items: flex-start;\n  justify-content: center;\n  flex-direction: column;\n  padding: 0 5px;\n  novo-dropdown {\n    button {\n      padding: 5px;\n      &:hover,\n      &:active,\n      &:focus {\n        background: rgba(0, 0, 0, 0.1) !important;\n      }\n    }\n  }\n}\n\n.novo-simple-checkbox-header-cell,\n.novo-simple-checkbox-cell {\n  display: flex;\n  align-items: flex-start;\n  justify-content: center;\n  flex-direction: column;\n  max-width: 40px;\n  padding: 0 10px;\n  > novo-checkbox {\n    margin-top: -4px;\n  }\n}\n\nnovo-activity-table {\n  position: relative;\n  width: 100%;\n  display: flex;\n  flex-direction: column;\n  flex: 1;\n  &.loading {\n    min-height: 300px;\n  }\n  header {\n    padding: 5px;\n    display: flex;\n    align-items: center;\n    flex-shrink: 0;\n    border-bottom: 1px solid $off-white;\n    > [novo-activity-table-custom-header] {\n      flex: 1;\n    }\n    > novo-search {\n      padding-right: 10px;\n      display: none;\n      @media (min-width: $breakpoint) {\n        display: flex;\n      }\n      > input {\n        padding: 8.5px;\n        font-size: 1.1em;\n        height: 35px;\n      }\n      &.active > button[theme=\"fab\"] {\n        height: 35px;\n      }\n    }\n    > div.novo-activity-table-actions {\n      flex: 1;\n      display: flex;\n      align-items: center;\n      justify-content: flex