label-long,\n  > .novo-simple-table-range-label-short {\n    padding-right: 10px;\n  }\n  > .novo-simple-table-range-label-long {\n    display: none;\n    @media (min-width: $breakpoint) {\n      display: block;\n    }\n  }\n  > .novo-simple-table-range-label-short {\n    display: block;\n    @media (min-width: $breakpoint) {\n      display: none;\n    }\n  }\n  > button:first-of-type {\n    margin-right: 5px;\n  }\n  > button {\n    span {\n      display: none;\n      @media (min-width: $breakpoint) {\n        display: block;\n      }\n    }\n  }\n  > button[theme][theme=\"dialogue\"][icon][side=\"left\"] {\n    padding: 5px;\n    @media (min-width: $breakpoint) {\n      padding: 5px 15px 5px 5px;\n    }\n  }\n  > button[theme][theme=\"dialogue\"][icon][side=\"right\"] {\n    padding: 5px;\n    @media (min-width: $breakpoint) {\n      padding: 5px 5px 5px 15px;\n    }\n  }\n}\n\n.simple-table-dropdown {\n  @extend .table-dropdown;\n  .header {\n    padding: 5px 10px 0px 10px;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    button {\n      padding: 3px;\n      icon {\n        font-size: 0.8em;\n      }\n    }\n  }\n  list item.active {\n    background: transparent;\n    font-weight: 500;\n  }\n}\n","@import \"../../styles/variables\";\n\n.tabbed-group-picker-button {\n  .tabbed-group-picker-button-label {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    min-width: 100px;\n    width: 100%;\n  }\n}\n\n.tabbed-group-picker-search {\n  width: 100%;\n  padding: 0.5em;\n  border-bottom: 0.1em solid #ccc;\n  position: relative;\n  input {\n    font-size: 1.2em;\n    background: transparent;\n    border: none;\n    border-radius: 0;\n    outline: none;\n    height: 2rem;\n    width: 95%;\n    margin: 0 0 0 1em;\n    padding: 0;\n    box-shadow: none;\n    box-sizing: content-box;\n    transition: all 300ms;\n    color: $dark;\n    &::placeholder {\n      color: var(--form-placeholder);\n    }\n  }\n  i.bhi-search,\n  i.bhi-times {\n    position: absolute;\n    bottom: 1em;\n    right: 0.5em;\n    color: $grey;\n    font-size: 1.2rem;\n    margin-right: 1em;\n  }\n  i.bhi-times {\n    cursor: pointer;\n  }\n}\n\n.tabbed-group-picker-column-container {\n  display: flex;\n  flex-direction: row;\n\n  .tabbed-group-picker-column {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n\n    &.left {\n      border-right: 1px solid $light;\n      justify-content: space-between;\n\n      .clear-all-button {\n        margin-bottom: 0.5em;\n      }\n\n      novo-nav {\n        overflow: auto;\n\n        > novo-tab {\n          min-height: 3em;\n\n          > .novo-tab-link {\n            max-width: 100%;\n            height: 100%;\n            display: flex;\n            justify-content: space-between;\n            align-items: center;\n          }\n          &.active {\n            .novo-tab-link {\n              font-weight: 400;\n            }\n            &:hover {\n              .novo-tab-link {\n                font-weight: 400;\n                color: $positive;\n              }\n            }\n          }\n          &:hover {\n            .novo-tab-link {\n              font-weight: 400;\n              color: $black;\n              opacity: 1;\n            }\n          }\n        }\n      }\n    }\n\n    &.right {\n      display: block;\n      width: 23em;\n      overflow: hidden;\n\n      .quick-select {\n        font-weight: 500;\n\n        .quick-select-label {\n          padding: 0.7em 1.9em 0 1.9em;\n          background: lighten($light, 10%);\n          text-transform: uppercase;\n          font-size: 0.8em;\n        }\n\n        .quick-select-list {\n          .quick-select-item {\n            background: lighten($light, 10%);\n            padding: 0.5em 1.75em;\n            border-bottom: none;\n          }\n        }\n      }\n      novo-optgroup {\n        cdk-virtual-scroll-viewport {\n          height: 27em;\n          overflow-x: hidden;\n          @media (max-height: 720px) {\n            height: 18em;\n          }\n        }\n\n        novo-option {\n          &:hover.novo-option-inert {\n            background: var(--background-main, rgba($ocean, 0.1));\n          }\n        }\n      }\n\n      .tabbed-group-picker-empty-item {\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n        justify-content: center;\n        color: $grey;\n        width: 100%;\n        height: 27em;\n\n        i {\n          padding-bottom: 0.2em;\n          font-size: 3em;\n        }\n        .empty-item-main-message {\n          font-weight: 500;\n          color: $dark;\n        }\n        .empty-item-sub-message {\n          font-size: 0.9em;\n        }\n      }\n    }\n  }\n}\n","// --------------------------------------------------\n// Global\n// --------------------------------------------------\n@import \"~novo-design-tokens/scss\";\n@import \"novo-design-tokens/css/variables.min.css\";\n\n@import \"./styles/variables\";\n@import \"./styles/reset\";\n@import \"./styles/base\";\n@import \"./styles/global/functions\";\n@import \"./styles/global/include-media\";\n@import \"./styles/global/flex-grid\";\n\n// --------------------------------------------------\n// Content\n// --------------------------------------------------\n@import \"./styles/typography\";\n@import \"./styles/iconography\";\n@import \"./elements/common/typography/text.mixins\";\n\n// CDK\n@import \"./styles/cdk\";\n@include cdk-overlay();\n\n// --------------------------------------------------\n// Elements\n// --------------------------------------------------\n@import \"./elements/data-table/data-table.component\";\n@import \"./elements/tabs/Tabs\";\n@import \"./elements/ace-editor/AceEditor\";\n@import \"./elements/radio/Radio\";\n@import \"./elements/toast/Toast\";\n@import \"./elements/modal/modal.component\";\n@import \"./elements/card/Card\";\n@import \"./elements/agenda/month/AgendaMonthView\";\n@import \"./elements/agenda/week/AgendaWeekView\";\n@import \"./elements/agenda/day/AgendaDayView\";\n@import \"./elements/agenda/common/AgendaDateChange\";\n@import \"./elements/loading/Loading\";\n@import \"./elements/loading/NovoSpinner\";\n@import \"./elements/select/Select\";\n@import \"./elements/picker/Picker\";\n@import \"./elements/picker/extras/grouped-multi-picker-results/GroupedMultiPickerResults\";\n@import \"./elements/picker/extras/mixed-multi-picker-results/MixedMultiPickerResults\";\n@import \"./elements/picker/extras/skills-picker-results/SkillsSpecialtyPickerResults\";\n@import \"./elements/chips/Chips\";\n@import \"./elements/dropdown/Dropdown\";\n@import \"./elements/switch/Switch\";\n@import \"./elements/date-picker/DatePicker\";\n@import \"./elements/time-picker/TimePicker\";\n@import \"./elements/date-time-picker/DateTimePicker\";\n@import \"./elements/header/Header\";\n@import \"./elements/list/List\";\n@import \"./elements/table/Table\";\n@import \"./elements/table/extras/pagination/Pagination\";\n@import \"./elements/dragula/Dragula\";\n@import \"./elements/form/Form\";\n@import \"./elements/quick-note/QuickNote\";\n@import \"./elements/tiles/Tiles\";\n@import \"./elements/tooltip/Tooltip\";\n@import \"./elements/slider/Slider\";\n@import \"./elements/tip-well/TipWell\";\n@import \"./elements/ckeditor/CKEditor\";\n@import \"./elements/category-dropdown/CategoryDropdown\";\n@import \"./elements/multi-picker/MultiPicker\";\n@import \"./elements/popover/PopOver\";\n@import \"./elements/table/extras/dropdown-cell/DropdownCell\";\n@import \"./elements/search/SearchBox\";\n@import \"./elements/places/places.component\";\n@import \"./elements/value/Values\";\n@import \"./elements/icon/Icon\";\n// @import \"./elements/expansion/expansion-panel\";\n// @import \"./elements/expansion/expansion-panel-header\";\n@import \"./elements/stepper/stepper.component\";\n@import \"./elements/stepper/step-header.component\";\n@import \"./elements/simple-table/table\";\n@import \"./elements/tabbed-group-picker/TabbedGroupPicker\";\n\n.bgc-main,\n.novo-background-main {\n  background-color: var(--background-main, $color-bright);\n}\n.bgc-alt,\n.bgc-bright,\n.novo-background-bright {\n  background-color: var(--background-bright, $color-white);\n}\n.bgc-dark,\n.novo-background-dark {\n  background-color: var(--background-dark, $color-white);\n}\n.bgc-muted,\n.novo-background-muted {\n  background-color: var(--background-muted, $color-white);\n}\n.bgc-body,\n.novo-background-body {\n  background-color: var(--background-body, $color-silver);\n}\n.txc-main {\n  color: var(--text-main, $color-charcoal);\n}\n.txc-muted {\n  color: var(--text-muted, $color-neutral);\n}\n.txc-disabled {\n  color: var(--text-disabled, $color-slate);\n}\n\n@include theme-colors() using ($name, $color, $contrast, $tint, $shade, $pale) {\n  $excluded: (bright, muted);\n\n  @if index($excluded, $name) == null {\n    .novo-background-#{$name} {\n      background: $color;\n      color: $contrast;\n    }\n    .novo-text-#{$name} {\n      color: $color;\n    }\n\n    .bgc-#{$name} {\n      background: $color;\n      color: $contrast;\n    }\n\n    .bgc-#{$name}-striped {\n      & > *:nth-child(odd) {\n        background: $color;\n      }\n    }\n    .txc-#{$name} {\n      color: $color;\n    }\n  }\n}\n\n@each $key, $value in $spacing-rules {\n  .padding-#{$key} {\n    padding: $value;\n  }\n  .px-#{$key} {\n    padding-left: $value;\n    padding-right: $value;\n  }\n  .py-#{$key} {\n    padding-top: $value;\n    padding-bottom: $value;\n  }\n  .margin-#{$key} {\n    margin: $value;\n  }\n  .ml-#{$key},\n  .mx-#{$key} {\n    margin-left: $value;\n  }\n  .mr-#{$key},\n  .mx-#{$key} {\n    margin-right: $value;\n  }\n  .mt-#{$key},\n  .my-#{$key} {\n    margin-top: $value;\n  }\n  .mb-#{$key},\n  .my-#{$key} {\n    margin-bottom: $value;\n  }\n}\n\n.modal-overlay-backdrop,\n.aside-overlay-backdrop {\n  background-color: rgba(0, 0, 0, 0.8);\n}\n\n.aside-overlay-panel {\n  position: relative;\n  // z-index: 1001;\n}\n\n@keyframes iconEnter {\n  0% {\n    opacity: 0;\n    transform: scale(0.8);\n  }\n  90% {\n    opacity: 1;\n    transform: scale(1.1);\n  }\n  100% {\n    opacity: 1;\n    transform: scale(1);\n  }\n}\n\n// Animations\n// -------------------------\n@keyframes rotate {\n  0% {\n    transform: rotateZ(0deg);\n  }\n\n  75% {\n    transform: rotateZ(200deg);\n  }\n\n  100% {\n    transform: rotateZ(180deg);\n  }\n}\n\n@keyframes half-rotate {\n  0% {\n    transform: rotateZ(45deg);\n  }\n\n  75% {\n    transform: rotateZ(100deg);\n  }\n\n  100% {\n    transform: rotateZ(90deg);\n  }\n}\n\n@keyframes rotateBack {\n  0% {\n    transform: rotateZ(90deg);\n  }\n\n  100% {\n    transform: rotateZ(0deg);\n  }\n}\n\n@keyframes show {\n  0% {\n    opacity: 0;\n    transform: translateX(-100%);\n  }\n\n  75% {\n    transform: translateX(0px);\n  }\n\n  100% {\n    opacity: 1;\n    transform: translateX(0);\n  }\n}\n","// App Container Sizing Reset\nhtml {\n  height: 100%;\n  width: 100%;\n  body {\n    height: 100%;\n    width: 100%;\n    demo-app {\n      height: 100%;\n      width: 100%;\n    }\n  }\n}\n\n*,\n*:after,\n*:before {\n  box-sizing: border-box;\n}\n","// App general body styles\nhtml {\n  margin: 0;\n  padding: 0;\n  scroll-behavior: smooth;\n\n  body {\n    margin: 0;\n    padding: 0;\n    position: relative;\n    overflow: hidden;\n    novo-demo-app {\n      height: 100%;\n      width: 100%;\n      box-sizing: border-box;\n      display: block;\n    }\n  }\n\n  .markdown-page > h2 {\n    margin-top: 5rem;\n    margin-bottom: 2rem;\n    &:not(:first-child) {\n      border-top: 2px solid #ccc;\n    }\n  }\n}\n\ncode-snippet,\nmulti-code-snippet {\n  margin-bottom: 20px;\n}\n\n.cover-img {\n  background: #aaa;\n  width: 100%;\n  max-height: 340px;\n  object-fit: contain;\n}\n","/* Contains all Design Page /Component Demo Page SCSS imports\nstarting at line 82. >>\n=============================================================\n*/\n\n// Main content container\n.main-content {\n  position: relative;\n  min-height: 300px;\n  .container {\n    padding: 2rem;\n    margin: 0 auto;\n    max-width: 840px;\n    h1 {\n      small {\n        font-size: 14px;\n        vertical-align: middle;\n      }\n    }\n  } // Contains component demos\n  .example {\n    margin-top: 10px;\n    border: 1px solid var(--border, #e9eaea);\n    box-sizing: border-box;\n  } // Contains code snippet\n  .example-container {\n    padding: 20px 30px;\n    margin: 0 auto;\n  } // Contains component demos\n  pre {\n    margin: 0 0 35px;\n    box-sizing: border-box;\n    font-size: 0.9em;\n    background: var(--background, #f8f8f8);\n    border: 1px solid var(--border, #e9eaea);\n    color: #4c545a;\n    code {\n      border: none;\n      width: 100%;\n      display: block;\n      box-sizing: border-box;\n      border-bottom-left-radius: 3px;\n      border-bottom-right-radius: 3px;\n      line-height: 1.5em;\n      padding: 15px 25px;\n      &.hljs {\n        background: var(--background) !important;\n      }\n    }\n  } // DESIGN STYLES\n  header {\n    &.design {\n      display: flex;\n      flex-direction: row;\n      justify-content: space-between;\n      > * {\n        width: 50%;\n      }\n\n      img {\n        max-height: 250px;\n        margin-top: auto;\n        margin-bottom: auto;\n      }\n    }\n  }\n  section {\n    &.design {\n      background: $white;\n    }\n    > aside {\n      background: $background;\n      padding: 10px 20px;\n      border-radius: 3px;\n      display: inline-block;\n      border: 2px solid darken($background, 2%);\n      h6 {\n        color: $mint;\n        margin-bottom: 5px;\n      }\n      &.design-principle {\n        h6 {\n          border-bottom: 1px solid darken($background, 5%);\n          padding-bottom: 8px;\n          span {\n            font-weight: 300;\n          }\n        }\n        .hint {\n          display: block;\n          color: darken($backgroun